.copyright__text {
  line-height: 18px;
  color: $dark-grey;
  text-decoration: none;

  @media (min-width: $tablet-width) {
    padding-bottom: 25px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 18px;
  }
}

.copyright__link {
  display: block;

  @media (min-width: $tablet-width) {
    margin-left: 22px;
    margin-bottom: 14px;
  }
}
